<template>
  <div class="dentistry">
    <dentistry-options @search="search" ref="dentistryOptions"/>
    <dentistry-table :denTotalSize="totalSize" :loading="loading" @search="search" :data="data" :pageShow="pageShow" ref="dentistryTable"/>
  </div>
</template>

<script>
import DentistryOptions from './controls/dentistryOptions'
import DentistryTable from './controls/dentistryTable'
import queryString from "querystring"

export default {
  name: 'dentistry',
  data() {
    return {
      data:[], //table数据
      totalSize: 0, //一共多少条数据
      pageShow: false,  //页码切换是否显示
      loading: false
    }
  },
  components: {
    DentistryOptions,
    DentistryTable
  },
  mounted(){
    this.search();
  },
  methods:{
    search(){  //搜索
      this.loading = true;
      const optionParams = this.$refs.dentistryOptions.getData();
      const tableParams = this.$refs.dentistryTable.getData();
      let params = this.lodash.assign({}, optionParams, tableParams);
      const param = queryString.stringify(params || {});
      this.api.get(`/dentistry?${param}`).then((res)=>{
        this.loading = false;
        if(!res.data) return;
        this.data = res.data.list;
        this.totalSize = res.data.totalPage;
        this.pageShow = this.data.length > 20
      }).catch((err) => {
        console.log(err)
        this.$message(err.message)
      })
    }
  }
}
</script>
 
